const slider = () => {
	const itemsParent = document.querySelector('.slider__items');
	const labels = document.querySelectorAll('.slider__nav-item');
	const labelLines = document.querySelectorAll('.slider__line');
	const length = labels.length;


	const scrollToByIndex = index => {
		const elementWidth = Number(getComputedStyle(itemsParent).width.slice(0, -2));
		itemsParent.scrollLeft = elementWidth * index;

		for (let i = 0; i < labels.length; i++) {
			labels[i].classList.remove('slider__nav-item_selected');
		}
		labels[index].classList.add('slider__nav-item_selected');
	};

	const count = (index, pause) => {
		const progressLine = document.createElement('div');
		progressLine.classList.add('slider__progress-line');
		progressLine.style.transition = `width ${pause}ms linear`;
		labelLines[index].appendChild(progressLine);

		progressLine.style.width = '100%';
		setTimeout(() => {
			labelLines[index].removeChild(progressLine);
		}, pause);
	};

	const autoPlay = {
		interval: null,
		pause: 4000,
		delay: 500,
		play(startIndex) {
			let i = startIndex;
			this.interval = setInterval(() => {
				scrollToByIndex(i);
				count(i, this.pause);
				i++;
				if (i === length) {
					i = 0;
				}
			}, this.pause);
		},
		stop() {
			clearInterval(this.interval);
			clearInterval(this.countInterval);
		}
	};

	autoPlay.play(0);

	for (let i = 0; i < length; i++) {
		labels[i].addEventListener('click', event => {
			scrollToByIndex(i);
			autoPlay.stop();
			autoPlay.play(i);
			event.preventDefault();
		});
	}
};

module.exports = slider;



